.navbar{
    background: rgb(30,30,30);
    /* background: linear-gradient(  #3399cc, #8226b0 ); */
    /* background: #3399cc; */
    height:5rem;
    width:100vw;
    margin: 0 auto;
    padding:1rem 2rem;
    max-width: 75rem;
    display:flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top:0;
    z-index: 3;
}
.logo{
    object-fit: contain;
    /* height:2.5rem; */
    width:2.5rem;
    border-radius: 50%;
}
.profile{
    width:40px;
    height:40px;
    border-radius: 50%;
}
.desktopMenuListItem{
    margin:1.5rem;
    cursor:pointer;
    position: relative;
    left: 1.5cm;
}
.desktopMenuListItem:hover{
    color:aqua;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgb(69, 141, 141);
}
.desktopMenuBtn{
    background: white;
    color:black;
    border:none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding:0 1rem;
    border-radius: 1rem;
}
.desktopMenuImg{
    object-fit: cover;
    height:1rem;
    width:1rem;
    margin: 1rem;
}
.active {
    color:aqua;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid aqua;

}
.mobMenu{
    display: none;
    object-fit: cover;
    height:1rem;
}
.navMenu{
    position: absolute;
    top:4rem;
    right:2rem;
    z-index:2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height:fit-content;
    min-width: 15rem;
    background: rgb(40,40,40);
    
    border-radius: 1rem;
}
.ListItem{
    color:white;
    padding: 0.5rem 3rem;
    margin:0.25rem;
    background: rgb(30,30,30);
}
@media screen and (max-width:720px){
    .mobMenu{
        display:flex;
    }
    .desktopMenu{
        display: none;
    }
    .desktopMenuBtn{
        display:none;
    }
}